import React, { FC } from "react";
import * as LucideIcon from "lucide-react";
import styled, { css } from "styled-components";

import { colors } from "../../styleguide";
import uuid from "../../utils/uuid";

export type IconName = keyof typeof LucideIcon;
/** @deprecated this component has been deprecated */
export type StatusVariants = "all" | "on" | "off" | "test" | "error";

const statusVariant: Record<StatusVariants, string> = {
	all: `${colors.midBlue}`,
	on: `${colors.teal}`,
	off: `${colors.darkerGray}`,
	test: `${colors.yellow}`,
	error: `${colors.red}`,
};

interface StyledStatusIndicatorProps {
	variant?: StatusVariants
	icon?: IconName
}

interface StatusIndicatorProps extends StyledStatusIndicatorProps {
	dataQA: string
	ariaLabel: string
	id?: string
	iconSize?: number
	className?: string
}

const StyledStatusIndicator = styled.div<StyledStatusIndicatorProps>`
	display: inline-block;
	margin-right: 8px;
	${({ variant }) => variant
		? css`background-color: ${statusVariant[variant]}`
		: css`background-color: ${colors.black}`};
	${({ variant, icon }) => icon
		? css`
		background-color: transparent;
		color: ${variant && statusVariant[variant]};
		span {
		margin-left: 0;
		}
	`
		:		css`
		height: 10px;
		width: 10px;
		border-radius: 10px;
		cursor: default;
	`
}`;

const IconWrapper = styled.span`
	vertical-align: middle;
	margin-left: 5px;
`;
/** @deprecated this component has been deprecated */
const StatusIndicator: FC<StatusIndicatorProps> = ({
	id,
	icon,
	variant = "all",
	ariaLabel,
	dataQA,
	iconSize = 12,
	className,
}) => {
	const MyIcon = icon ? LucideIcon[icon] as FC<{ size: number }> : LucideIcon.Circle;

	return (
		<StyledStatusIndicator
			icon={icon}
			variant={variant}
			aria-label={ariaLabel}
			id={id || uuid()}
			data-qa={dataQA}
			className={className}
		>
			{ icon && <IconWrapper><MyIcon size={iconSize} strokeWidth={3} /></IconWrapper> }
		</StyledStatusIndicator>
	);
};

export default StatusIndicator;
